@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.main {
    display: flex;
    padding: 50px 30px 0 30px;
    flex-wrap: wrap;
}
.card-content {
    width: 250px;
    height: 250px;
    border-radius: 10px;
    box-shadow: 4px 3px 7px 1px #ccc;
    padding: 25px 10px 10px 15px;
    position: relative;
    margin-right: 30px;
    margin-bottom: 33px;
}

.flag {
    width: 56px;
}

.card-content h1 {
    font-size: 16px;
    font-weight: 700;
    line-height: 18,75px;
    color: #4F9419;
    text-transform: uppercase;
    margin: 5px 0 10px 0;
    font-family: 'Roboto', sans-serif;
}

.card-info {
    margin-top: 40px;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
}

.card-info p {
    margin-bottom: 10px;
}

.edit,
.del {
    width: 14px;
    position: absolute;
    cursor: pointer;
}

.del {
    top: 20px;
    right: 20px;
}

.edit {
    top:20px;
    right: 60px;
}

.header {
    width: 100%;
    height: 85px;
    background-color: black;
    margin-top: -3px;
    position: relative;
}

.logo {
    width: 152px;
    position: absolute;
    left: 53px;
}
.search {
    background-color: #4F9419;
    width: 100%;
    min-height: 190px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 20px 0 30px 0;
}

.label {
    display: flex;
    flex-direction: column;
    margin-right: 20px;
    justify-content: center;
    align-items: flex-start;
    color: white;
}

.input {
    height: 48px;
    border-radius: 7px;
    padding: 10px;
}

.search-pais {
    width: 300px;
    background-color: white;
    border: none;
}

.search-local {
    width: 455px;
    border: none;
}

.search-data {
    width: 238px;
    border: none;
    border: none;
}

.search-btn {
    width: 203px;
    height: 49px;
    padding: 10px 30px;
    color: white;
    background-color: #006C18;
    border-radius: 7px;
    margin-top: 15px;
    border: none;
}

.search-btn:hover {
    cursor: pointer;
}
